.portfolio__container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 2.5rem;
}

.portfolio__item{
    background: transparent;
    padding: 1.2rem;
    border-radius: 3rem 3rem 0rem 0rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover{
    background: var(--color-bg-variant);
    border-radius: 20rem 20rem 0rem 0rem;
   border-color: var(--color-primary-variant);
}

.portfolio__item-image{
    border-radius: 3rem 3rem 0rem 0rem;
    min-height: 100%;
    background-size: cover;
    overflow: hidden;
    transition: var(--transition);

}
.portfolio__item-image:hover{
    border-radius: 20rem 20rem 0rem 0rem;
    box-shadow:inset 0 0 0 3000px rgba(0, 0, 0, 0.778);
  }
.portfolio__item h2{
    color: transparent;
    font-size: 2rem;
    margin: 1.1rem 0 2rem;
    text-align: center;
    line-height: 30rem;
    transition: var(--transition);

}
.portfolio__item h2:hover{
    color: var(--color-light);
    font-size: 2.5rem;
    text-align: center;
}

.portfolio__item-cta{
    margin: 0rem 0 5rem;
    justify-content: center;
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
}
/* =================== Large QUERIES (HD DEVICES) ====================== */
@media screen and (max-width: 1920px){
    .portfolio__container{
        gap: 4rem;
    }
    .portfolio__item{
     border-radius: 3rem 3em 0rem 0rem;
     height: 25rem;
     }
    .portfolio__item:hover{
     border-radius: 15rem 15rem 0rem 0rem;
     }
     .portfolio__item-image{
         border-radius: 3rem 3rem 0rem 0rem;
         height: 10%;
     }
     .portfolio__item-image:hover{
        border-radius: 15rem 15rem 0rem 0rem;
    }
   }

/* =================== MEDIA QUERIES (MEDIUM DEVICES) ====================== */
@media screen and (max-width: 1024px){
   .portfolio__container{
       grid-template-columns: 1fr 1fr;
       gap: 4rem;
   }
   .portfolio__item{
    height: 20rem;
    }
   .portfolio__item:hover{
    border-radius: 12rem 12rem 0rem 0rem;
    }
    .portfolio__item-image{
        border-radius: 10rem 10rem 0rem 0rem;
        height: 10%;
    }
  }
  /* =================== MEDIA QUERIES (Small DEVICES) ====================== */
  @media screen and (max-width: 600px){
    .portfolio__container{
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    .portfolio__item{
        height: 15rem;

    }
    .portfolio__item:hover{
        border-radius: 5rem 5rem 0rem 0rem;
        height: 15rem;

    }
    .portfolio__item-image{
        border-radius: 5rem 5rem 0rem 0rem;
        height: 10%;
    }
  }
