@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap');
*{
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #2b1b12;
  --color-bg-variant: #2c1b0b;
  --color-primary: #a97339;
  --color-primary-variant: rgba(240, 120, 7, 0.4);
  --color-white: #fff;
  --color-light: #977f76;
  --color-background: #000000;

  --transition: all 500ms ease;
  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;

}

html{
  scroll-behavior: smooth;
}

::-webkit-scrollbar{
  display: none;
}
@font-face {
  font-family: 'Neue Haas Grotesk';
  src: url('./assets/NHaasGroteskTXPro-55Rg.woff') format('woff');

}  
body {
  font-family: 'Neue Haas Grotesk';
  /*font-family: 'PT Sans', sans-serif;*/
  margin: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;
  background-color: var(--color-background);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* =================== light Theme ============== */
@media (prefers-color-scheme: light) {

  :root {
    --color-bg: #ffffff;
    --color-background: #ffffff;
    --color-bg-variant: #ececec;
    --color-primary: #e28834;
    --color-primary-variant: rgba(255, 223, 194, 0.4);
    --color-white: rgb(0, 0, 0);
    --color-light: #7b4633;
  }
  .Hero__Image{
    display: none;
  }

}
/* =--=-=-==-=-=-=-=-=-=-=- Full HD =-=-=-=-=-=-=-=- */
.container{
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,h2,h3,h4{
  font-weight: 700;
}

h2,h3,h4{
  font-family: 'Playfair Display', serif;

}
h1{
  font-weight: 400;
  font-size: 2.5rem;
}
h2{
  font-size: 4.5rem;
}
h3{
  font-size: 1.5rem;
}
section{
  margin-top:8rem;
}

section > h2, section > h5{
  text-align: center;
  color: var(--color-light);
}

section > h2{
  color: var(--color-light);
  margin-bottom: 3rem;
}

.text-light{
  color: var(--color-light);
}

a{
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover{
  color: var(--color-white);
}

.btn{
  width:max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 1.5rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover{
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;  
  
}
.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}
.btn__holder{
  text-align: center;
}
img{
  display: block;
  width: 100%;
  object-fit: cover;
}
.Hero__Image{
  width: 15%;
  margin-top: -30rem;
  margin-left: 10%;
  pointer-events:none;
  z-index: -3;
}

/* =================== Large device ====================== */
@media screen and (max-width: 1920px){

}

/* =================== Medium device ====================== */
@media screen and (max-width: 1024px){
  .container{
    width: var(--container-width-md);
  }

  section{
    margin-top: 6rem;
  }

}
/* =================== Small device ====================== */
@media screen and (max-width: 600px){
  .container{
    width: var(--container-width-sm);
  }
  h2{
    font-size: 3.5rem;
  }
  section > h2{
    margin-bottom: 2rem;
  }

}
/* =================== Tiny device ====================== */
@media screen and (max-width: 400px){
  .container{
    width: var(--container-width-sm);
  }

  section > h2{
    margin-bottom: 1rem;
  }
  .Hero__Image{
    width: 75%;
    max-width: 100%;
    height: auto;
  }
}

/* ================== LOCOMOTIVE CSS =============*/
html.has-scroll-smooth {
  overflow: hidden; }

html.has-scroll-dragging {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.has-scroll-smooth body {
  overflow: hidden; }

.has-scroll-smooth [data-scroll-container] {
  min-height: 100vh; }

[data-scroll-direction="horizontal"] [data-scroll-container] {
  height: 100vh;
  display: inline-block;
  white-space: nowrap; }

[data-scroll-direction="horizontal"] [data-scroll-section] {
  display: inline-block;
  vertical-align: top;
  white-space: nowrap;
  height: 100%; }

.c-scrollbar {
  position: absolute;
  right: 0;
  top: 0;
  width: 11px;
  height: 100%;
  transform-origin: center right;
  transition: transform 0.3s, opacity 0.3s;
  opacity: 0; }
  .c-scrollbar:hover {
    transform: scaleX(1.45); }
  .c-scrollbar:hover, .has-scroll-scrolling .c-scrollbar, .has-scroll-dragging .c-scrollbar {
    opacity: 1; }
  [data-scroll-direction="horizontal"] .c-scrollbar {
    width: 100%;
    height: 10px;
    top: auto;
    bottom: 0;
    transform: scaleY(1); }
    [data-scroll-direction="horizontal"] .c-scrollbar:hover {
      transform: scaleY(1.3); }

.c-scrollbar_thumb {
  position: absolute;
  top: 0;
  right: 0;
  background-color: black;
  opacity: 0.5;
  width: 7px;
  border-radius: 10px;
  margin: 2px;
  cursor: -webkit-grab;
  cursor: grab; }
  .has-scroll-dragging .c-scrollbar_thumb {
    cursor: -webkit-grabbing;
    cursor: grabbing; }
  [data-scroll-direction="horizontal"] .c-scrollbar_thumb {
    right: auto;
    bottom: 0; }