nav{
    background: rgba(86, 45, 4, 0.5);
    width: max-content;
    display: block;
    padding: 1rem 5rem;
    z-index: 2;
    position: fixed;
    bottom: 50%;
    left: 2%;
    gap: 2rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 0.4rem;
    border-radius: 50%;
    display: flex;
    font-size: 2rem;
    transition: var(--transition);
    color: var(--color-light);
}

nav a:hover{
    background: rgba(0,0,0,0.3);
}
nav a.active{
    background: var(--color-bg);
    color: var(--color-white);
}

/* ========== Light Theme ============== */
@media (prefers-color-scheme: light) {
    nav a:hover{
        background: rgba(255, 255, 255, 0.3);
    }
    nav a.active{
        background: var(--color-primary);
        color: var(--color-bg);
    }
}

/* =================== MEDIA QUERIES (MEDIUM DEVICES) ====================== */
@media screen and (max-width: 1024px){
    nav{
        padding: 1rem 3rem;
        gap: 0.5rem;
        bottom: 0.5rem;
        display: flex;
        left: 31%;
    }
}
/* =================== MEDIA QUERIES (SMALL DEVICES) ====================== */
@media screen and (max-width: 600px){
    nav{
        padding: 1rem 3rem;
        gap: 0.5rem;
        bottom: 0.5rem;
        display: flex;
        left: 10%;
    }

}